export var newMineGrid = function (row, col, mines) {
    var grid = [];
    for (var i = 0; i < row; i++) {
        grid[i] = [];
        for (var j = 0; j < col; j++) {
            grid[i][j] = 0;
        }
    }
    while (mines) {
        var i = Math.floor(Math.random() * row);
        var j = Math.floor(Math.random() * col);
        if (grid[i][j] !== -1) {
            grid[i][j] = -1;
            mines--;
            for (var x = i > 0 ? i - 1 : i; x <= (i == row - 1 ? i : i + 1); x++) {
                for (var y = j > 0 ? j - 1 : j; y <= (j == col - 1 ? j : j + 1); y++) {
                    if (grid[x][y] !== -1)
                        grid[x][y]++;
                }
            }
        }
    }
    return grid;
};
export var newViewGrid = function (row, col) {
    var grid = [];
    for (var i = 0; i < row; i++) {
        grid[i] = [];
        for (var j = 0; j < col; j++) {
            grid[i][j] = 0;
        }
    }
    return grid;
};
