var recurse = function (mineGrid, viewGrid, i, j, row, col, count) {
    viewGrid[i][j] = 1;
    count++;
    if (mineGrid[i][j] != 0)
        return count;
    for (var x = i > 0 ? i - 1 : i; x <= (i == row - 1 ? i : i + 1); x++) {
        for (var y = j > 0 ? j - 1 : j; y <= (j == col - 1 ? j : j + 1); y++) {
            if (viewGrid[x][y] === 0)
                count += recurse(mineGrid, viewGrid, x, y, row, col, 0);
        }
    }
    return count;
};
export var clicked = function (mineGrid, viewGrid, x, y, row, col) {
    var count = recurse(mineGrid, viewGrid, x, y, row, col, 0);
    console.log(count);
    return [viewGrid, count];
};
